
.docs-introduce {
  text-align: left;
}
.docs-introduce .introduce-title {
    font-size: 24px;
    line-height: 60px;
    height: 60px;
    border-bottom: 1px solid #ddd;
    font-weight: bold;
}
.docs-introduce .introduce-content .title {
    font-size: 18px;
    font-weight: bold;
    line-height: 32px;
    margin-top: 20px;
}
.docs-introduce .introduce-content .description {
    font-size: 14px;
    line-height: 22px;
    margin-top: 5px;
}
.docs-introduce .introduce-content .img {
    max-width: 100%;
    margin-top: 20px;
}



