
.user-icon-add .add-title {
  font-size: 24px;
  color: #336;
  line-height: 24px;
  height: 24px;
  font-weight: normal;
}
.user-icon-add .add-count {
  font-size: 14px;
  color: #ccc;
}
.user-icon-add .operator-box {
  margin: 40px auto;
}
.user-icon-add .submit-tip {
  font-size: 12px;
  color: #f20;
  line-height: 24px;
  text-align: center;
}
.user-icon-add .normal {
  color: #999;
}
.user-icon-add .operate-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.user-icon-add .submit {
  display: block;
  width: 160px;
  background: #333366;
  color: #fff;
  height: 44px;
  font-size: 16px;
  border: 1px solid #333366;
}
.user-icon-add .submit:hover {
    border: 1px solid #494993;
    background: #494993;
    color: #fff;
}
.user-icon-add .submit-colorful {
  margin-left: 30px;
  background: #f20;
  color: #fff;
  border: 1px solid #f20;
}
.user-icon-add .submit-colorful:hover {
    border: 1px solid #f20;
    background: #f22;
    color: #fff;
}



