
.common-icon-repo-card {
  width: 274px;
  padding: 11px;
  background: #fff;
  border-radius: 5px;
  margin-right: 20px;
  margin-bottom: 20px;
  text-align: center;
  display: inline-block;
  -webkit-transition: all .4s;
  transition: all .4s;
  position: relative;
  overflow: hidden;
  border: 1px solid #ccc;
}
.common-icon-repo-card:hover {
    -webkit-box-shadow: 2px 2px 8px rgba(22, 22, 22, 0.2), -2px -2px 12px rgba(22, 22, 22, 0.2);
            box-shadow: 2px 2px 8px rgba(22, 22, 22, 0.2), -2px -2px 12px rgba(22, 22, 22, 0.2);
}
.common-icon-repo-card .repo-card-in {
    color: inherit;
}
.common-icon-repo-card .repo-list {
    height: 170px;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    color: #666;
    display: block;
}
.common-icon-repo-card .repo .list-empty {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.common-icon-repo-card .repo .list-empty-in {
    font-size: 16px;
    text-align: center;
}
.common-icon-repo-card .repo .common-icon-item {
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: #666;
    float: left;
    margin-top: 4px;
    font-size: 32px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.common-icon-repo-card .repo-info-top {
    height: 30px;
    line-height: 30px;
    text-align: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.common-icon-repo-card .repo-info-bottom {
    height: 30px;
    line-height: 30px;
    text-align: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.common-icon-repo-card .repo-info [class^="icon-"] {
    font-size: 16px;
    vertical-align: middle;
}
.common-icon-repo-card .repo-info .delete, .common-icon-repo-card .repo-info .edit {
    cursor: pointer;
}
.common-icon-repo-card .repo-info .delete {
    margin-right: 4px;
}



