
.user-repo-list {
  width: 1140px;
  margin: 0 auto;
}
.user-repo-list .common-icon-repo-card {
    margin-right: 14px;
}
.user-repo-list .common-icon-repo-card:nth-child(4n) {
    margin-right: 0;
}
.user-repo-list .title {
    font-size: 24px;
    color: #336;
    line-height: 24px;
    height: 24px;
    font-weight: normal;
}
.user-repo-list .title .count {
      font-size: 14px;
      color: #ccc;
}
.user-repo-list .iconRepo-list {
    text-align: left;
}
.user-repo-list .user-create-btn {
    color: #333;
    font-size: 14px;
}
.user-repo-list .user-create-btn:hover {
      cursor: pointer;
}



