
.repo-add {
  margin: 0 auto;
  width: 960px;
  text-align: center;
}
.repo-add .invalid .ivu-input {
    border-color: #f20;
}
.repo-add .invalid .ivu-input:hover {
    -webkit-box-shadow: 0 0 0 2px rgba(255, 34, 0, 0.1);
            box-shadow: 0 0 0 2px rgba(255, 34, 0, 0.1);
}
.repo-add .validation-error {
    position: absolute;
    line-height: 28px;
}
.repo-add-title {
    font-size: 24px;
}
.repo-add-table .ivu-row {
    margin-top: 20px;
}
.repo-add-table .ivu-col-span-4 {
    text-align: right;
    height: 42px;
    line-height: 42px;
    font-size: 16px;
}
.repo-add-table .ivu-col-span-20 {
    line-height: 42px;
    text-align: left;
    padding-left: 25px;
    position: relative;
}
.repo-add-table .ivu-input-large {
    height: 42px;
}
.repo-add-submit {
    margin-top: 40px;
    width: 160px;
    background: #333366;
    color: #fff;
    height: 44px;
    font-size: 16px;
    border: 1px solid #333366;
}
.repo-add-submit:hover {
      border: 1px solid #494993;
      background: #494993;
      color: #fff;
}



