
body {
  background: #f8f8f8;
}
.docs {
  width: 1200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 30px auto 0;
}
.docs-side-menu {
    background: #fff;
}
.docs-side-menu .ivu-menu-light:after {
      display: none;
}
.docs-side-menu .ivu-menu-light.ivu-menu-vertical .ivu-menu-item {
      border-left: 2px solid transparent;
      border-right: none;
}
.docs-side-menu .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
      border-right: none;
      border-left: 2px solid #336;
      color: #336;
}
.docs-side-menu .ivu-menu-item {
      border: none;
      font-size: 18px;
}
.docs-content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    background: #fff;
    margin-left: 20px;
    padding: 20px 40px 60px;
}



