
.index-index-hello .index {
  margin: 100px auto 0;
  color: #336;
}
.index-index-hello .index .common-search-input {
    border: 1px solid #336;
    border-radius: 25px;
    overflow: hidden;
}
.index-index-hello .index .ivu-input {
    font-size: 16px;
    text-align: center;
    height: 50px;
}
.index-index-hello .index .ivu-input-group-append {
    background: #336;
    border: 1px solid #336;
    color: #fff;
    font-size: 17px;
    padding: 0 12px;
}
.index-index-hello .index .ivu-input-group-append .ivu-btn {
      font-size: 26px;
}
.index-index-hello .index-search {
  width: 766px;
  height: 50px;
  margin: 0 auto;
}
.index-index-hello .index-icon {
  font-size: 120px;
}
.index-index-hello .index-repo-list {
  width: 1200px;
  margin: 60px auto 120px;
  text-align: left;
}



