
.user-repo-detail {
  width: 1140px;
  margin: 0 auto;
}
.user-repo-detail .repo-icon-search {
    margin-right: 20px;
}
.user-repo-detail .common-search-input .ivu-input {
    border: 1px solid #dddee1;
}
.user-repo-detail .repo-detail {
    height: 32px;
    line-height: 32px;
    font-size: 14px;
    color: #333;
    text-align: left;
}
.user-repo-detail .repo-detail .info-item {
      margin-right: 15px;
}
.user-repo-detail .repo-detail .label {
      color: #999;
      font-size: 19px;
      vertical-align: middle;
}
.user-repo-detail .repo-detail .repo-sync, .user-repo-detail .repo-detail .add-icon, .user-repo-detail .repo-detail .add-member {
      margin-right: 20px;
      background: none;
      color: #333;
      border: 1px solid #ddd;
      padding: 6px 15px;
      font-size: 12px;
      border-radius: 4px;
      cursor: pointer;
}
.user-repo-detail .repo-detail .repo-sync {
      background: #336;
      color: #fff;
      border: 1px solid #336;
}
.user-repo-detail .repo-detail .add-icon {
      cursor: pointer;
      padding: 7px 15px;
}
.user-repo-detail .repo-detail .add-member-box {
      margin-right: 20px;
      width: 330px;
}
.user-repo-detail .repo-detail .member-select {
      width: 80px;
}
.user-repo-detail .repo-css {
    margin-top: 10px;
    width: 520px;
}
.user-repo-detail .user-icon-list {
    margin: 30px 0 60px;
}
.ivu-modal-body {
  min-height: 300px;
  padding: 16px 26px;
  max-height: 600px;
  overflow-y: auto;
}
.unique-icon-list .add-operator {
  height: 32px;
  line-height: 32px;
}
.unique-icon-list .common-search-input .ivu-input {
  border: 1px solid #dddee1;
}
.unique-icon-list .common-icon-list-box {
  margin-top: 0;
}
.unique-icon-list .common-icon-list-box .common-icon-card:nth-child(5n) {
    margin-right: 0;
}



