
.install {
  margin: 0 auto 120px;
  width: 960px;
  text-align: center;
}
.install .sub-name {
    font-size: 20px;
    text-align: center;
    margin-top: 60px;
}
.install .delete {
    cursor: pointer;
    font-size: 32px;
}
.install .description {
    font-size: 12px;
    color: #999;
    width: 720px;
    margin: 12px auto;
    text-align: justify;
}
.install .product-type {
    padding-top: 40px;
    text-align: left;
}
.install .validation-box {
    margin-top: 30px;
}
.install .add-item {
    position: absolute;
    height: 32px;
    width: 32px;
    text-align: center;
    line-height: 32px;
    font-size: 32px;
    color: #333366;
    bottom: 0;
    right: -30px;
}
.install .invalid .ivu-input {
    border-color: #f20;
}
.install .invalid .ivu-input:hover {
    -webkit-box-shadow: 0 0 0 2px rgba(255, 34, 0, 0.1);
            box-shadow: 0 0 0 2px rgba(255, 34, 0, 0.1);
}
.install .validation-error {
    position: absolute;
    line-height: 28px;
}
.install-title {
    font-size: 24px;
}
.install-table {
    position: relative;
    margin-top: 30px;
}
.install-table .ivu-row {
      margin-top: 12px;
}
.install-table .ivu-col-span-2, .install-table .ivu-col-span-1 {
      text-align: right;
      height: 32px;
      line-height: 32px;
      font-size: 12px;
}
.install-table .ivu-col-span-1 {
      text-align: center;
      line-height: 46px;
}
.install-table .ivu-col-span-6, .install-table .ivu-col-span-5 {
      line-height: 32px;
      text-align: left;
      padding-left: 10px;
      position: relative;
}
.install-table .ivu-input-large {
      height: 32px;
}
.install-submit {
    margin-top: 40px;
    width: 160px;
    background: #333366;
    color: #fff;
    height: 44px;
    font-size: 16px;
    border: 1px solid #333366;
}
.install-submit:hover {
      border: 1px solid #494993;
      background: #494993;
      color: #fff;
}



