
.docs-icon-maker {
  text-align: left;
}
.docs-icon-maker .maker-title {
    font-size: 24px;
    line-height: 60px;
    height: 60px;
    border-bottom: 1px solid #ddd;
    font-weight: bold;
}
.docs-icon-maker .contents-title {
    font-size: 14px;
    color: #333;
    margin-bottom: 20px;
    text-align: left;
}
.docs-icon-maker .contents-detail {
    text-align: left;
    margin-bottom: 50px;
}
.docs-icon-maker .contents-detail svg {
      vertical-align: middle;
      display: inline-block;
}
.docs-icon-maker .contents-detail .detail-right {
      display: inline-block;
      width: 500px;
      color: #999;
      font-size: 12px;
      line-height: 30px;
      vertical-align: middle;
      margin-left: 40px;
}



