
.user-icon-list {
  width: 1140px;
  margin: 0 auto;
}
.user-icon-list .common-icon-list-box {
    margin-top: 0;
}
.user-icon-list .list-title {
    font-size: 24px;
    color: #336;
    line-height: 24px;
    height: 24px;
    font-weight: normal;
}
.user-icon-list .list-count {
    font-size: 14px;
    color: #ccc;
}
.user-icon-list .user-icon-search {
    margin-right: 14px;
}
.user-icon-list .common-search-input .ivu-input {
    border: 1px solid #dddee1;
}
.user-icon-list .item-add, .user-icon-list .operator-all, .user-icon-list .add2lib {
    color: #333;
    font-size: 14px;
    height: 32px;
    line-height: 32px;
}
.user-icon-list .item-add:hover, .user-icon-list .operator-all:hover, .user-icon-list .add2lib:hover {
      cursor: pointer;
}
.user-icon-list .operator-all, .user-icon-list .add2lib, .user-icon-list .lib-list {
    margin-right: 10px;
}
.user-icon-list .operator-fill-items {
    margin-top: 30px;
    min-height: 33px;
    position: relative;
}
.user-icon-list .operator-fill-items .btn {
      display: inline-block;
      padding: 0 20px;
      height: 32px;
      line-height: 30px;
      border: 1px solid transparent;
      border-radius: 20px;
      text-align: center;
      white-space: nowrap;
      -ms-touch-action: manipulation;
      touch-action: manipulation;
      cursor: pointer;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      color: #333;
      background-color: #ddd;
      margin-right: 15px;
}
.user-icon-list .operator-fill-items .lib-list {
      border-radius: 0px;
      background: #fff;
      color: #333;
      padding: 0;
      position: relative;
      border: none;
      width: 130px;
}
.user-icon-list .operator-fill-items .lib-list .text {
        color: #333;
}
.user-icon-list .operator-fill-items .lib-list .ivu-select-dropdown {
        left: 0;
        right: 0;
}
.user-icon-list .operator-fill-items .select-all {
      background: #fff;
}



