
.top-bar {
  color: #fff;
  height: 60px;
  background: #333366;
}
.top-bar-center {
    width: 1160px;
    margin: 0 auto;
}
.top-bar-icon {
    height: 60px;
    font-size: 40px;
    color: #fff;
    line-height: 60px;
}
.top-bar-menu {
    margin-left: 40px;
}
.top-bar-menu .active:after {
      content: '';
      height: 3px;
      color: #fff;
      background: #fff;
      position: absolute;
      left: 15px;
      right: 15px;
      bottom: 8px;
}
.top-bar-menu-item {
      height: 60px;
      padding: 0 15px;
      font-size: 16px;
      position: relative;
}
.top-bar-menu-item .text {
        display: block;
        color: #ddd9db;
        line-height: 60px;
        cursor: pointer;
}
.top-bar-menu-item .text:hover {
          color: #fff;
}
.top-bar-menu-item .text:hover:after {
            content: '';
            height: 3px;
            color: #fff;
            background: #fff;
            position: absolute;
            left: 15px;
            right: 15px;
            bottom: 8px;
}
.top-bar-user {
    height: 60px;
    font-size: 28px;
    padding: 0 15px;
    color: #fff;
    line-height: 60px;
}
.top-bar-user a {
      color: #fff;
}
.top-bar-user .user {
      position: relative;
      top: 3px;
}
.top-bar-user-login {
      font-size: 0;
}
.top-bar-user-login span, .top-bar-user-login a {
        font-size: 16px;
        padding: 0 8px;
        cursor: pointer;
}
.top-bar-user .title {
      color: #495060;
      font-size: 12px;
      text-align: left;
      padding: 2px 10px 2px 10px;
}
.top-bar-user .entrance-item a {
      display: block;
      font-size: 12px;
      color: #495060;
      height: 32px;
      line-height: 32px;
      text-align: left;
      padding: 2px 10px 2px 10px;
}
.top-bar-user .entrance-item a:hover {
      background-color: #F3F4F6;
}
.top-bar-search {
    width: 180px;
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.top-bar-search .common-search-input {
      border-radius: 20px;
      overflow: hidden;
      width: 100%;
      border: 1px solid #fff;
      height: 28px;
}
.top-bar-search .ivu-input {
      background: none;
      color: #fff;
      padding: 4px 12px;
      font-size: 12px;
      height: 26px;
}
.top-bar-search .ivu-btn {
      padding: 0 12px;
}
.top-bar-search .ivu-input-group-append {
      background: none;
      border: none;
      color: #fff;
}



