
.user-page {
  padding-bottom: 120px;
}
.user-page .common-icon-list-box {
    min-height: 246px;
}
.user-page .user-info {
    margin: 0 auto;
    color: #fff;
    position: relative;
}
.user-page .user-info-bg {
      background: #333366;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      overflow: hidden;
      z-index: -1;
}
.user-page .user-info-bg-wrap {
        position: relative;
        top: 0;
        height: 100%;
        left: 0;
        width: 1360px;
        margin: 0 auto;
}
.user-page .user-info-bg .planet {
        width: 110px;
        height: 110px;
        left: 132px;
        top: 180px;
        opacity: .15;
        background: -webkit-gradient(linear, left bottom, left top, from(#ff5a25), to(#ff005a));
        background: linear-gradient(0deg, #ff5a25, #ff005a);
        z-index: 999;
        position: absolute;
        border-radius: 50%;
}
.user-page .user-info-bg .planet-10 {
        width: 60px;
        height: 60px;
        left: 432px;
        top: 30px;
        background: -webkit-gradient(linear, left bottom, left top, from(#ff314f), to(#ff1be6));
        background: linear-gradient(0deg, #ff314f, #ff1be6);
}
.user-page .user-info-bg .planet-11 {
        width: 110px;
        height: 110px;
        left: 1032px;
        top: 70px;
        background: -webkit-gradient(linear, left bottom, left top, from(#5821ff), to(#aaa8ff));
        background: linear-gradient(0deg, #5821ff, #aaa8ff);
}
.user-page .user-info-bg .planet-12 {
        width: 60px;
        height: 60px;
        left: 1532px;
        top: 140px;
        background: -webkit-gradient(linear, left bottom, left top, from(#ff314f), to(#ff1be6));
        background: linear-gradient(0deg, #ff314f, #ff1be6);
}
.user-page .user-info-box {
      padding: 60px 0 0 0;
      width: 1200px;
      margin: 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: left;
          -ms-flex-pack: left;
              justify-content: left;
}
.user-page .user-info-avatar {
      width: 130px;
      height: 130px;
      position: relative;
      background: url(https://gw.alicdn.com/tps/TB1W_X6OXXXXXcZXVXXXXXXXXXX-400-400.png) center center/cover no-repeat;
}
.user-page .user-info-avatar .head {
        width: 86px;
        height: 86px;
        border-radius: 50%;
        margin-top: 15px;
}
.user-page .user-info-avatar .cover {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
}
.user-page .user-info-detail {
      padding-top: 40px;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: left;
}
.user-page .user-info-detail .name {
        font-size: 24px;
        line-height: 22px;
        padding-left: 20px;
}
.user-page .user-info-detail .repo-tabs {
        margin-bottom: -1px;
        margin-top: 17px;
        width: 100%;
}
.user-page .user-info-detail .repo-tabs .ivu-menu-horizontal {
          background: none;
          color: #fff;
          height: 50px;
          line-height: 50px;
}
.user-page .user-info-detail .repo-tabs .ivu-menu-horizontal.ivu-menu-light:after {
          display: none;
}
.user-page .user-info-detail .repo-tabs .ivu-menu-item {
          color: #fff;
          font-size: 16px;
}
.user-page .user-info-detail .repo-tabs .ivu-menu-item:hover, .user-page .user-info-detail .repo-tabs .ivu-menu-item-active, .user-page .user-info-detail .repo-tabs ivu-menu-item-selected {
          border-bottom: 3px solid #fff;
}
.user-page .user-info-detail .repo-tabs .tab-item {
          height: 50px;
          width: 50px;
          line-height: 50px;
          color: #fff;
          font-size: 24px;
          border-radius: 50%;
          background: #9266f9;
          margin-left: 20px;
          position: relative;
          bottom: -25px;
          text-align: center;
}
.user-page-detail {
    width: 1200px;
    margin: 0 auto;
    padding-top: 40px;
    position: relative;
}
.user-page-detail .ivu-menu-item {
      text-align: right;
      font-size: 14px;
}
.user-page-detail .ivu-menu-item:hover {
      background: none;
      color: #336;
}
.user-page-detail .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
      color: #336;
      border-right: 3px solid #fff;
}
.user-page-detail .ivu-col-span-20 {
      text-align: left;
      padding-left: 80px;
}
.user-page-detail .ivu-col-span-20 .title {
        font-size: 24px;
        color: #336;
        line-height: 24px;
        height: 24px;
        font-weight: normal;
}
.user-page-detail .ivu-col-span-20 .title .count {
          font-size: 14px;
          color: #ccc;
}
.user-page-detail .ivu-col-span-20 .iconRepo-list {
        margin-top: 30px;
}
.user-page-detail .ivu-col-span-20 .list-page {
        text-align: center;
}



